import React from 'react';
import ReactModal from 'react-modal';
import LayoutModal from './LayoutModal';
import FieldsModal from './FieldsModal';
import FieldSettings from './FieldSettings';
import './index.css';

const Modal = (props) => {
	const modalProps = props.modalProps;
	let modal = '';
	let modalClasses = '';
	//console.log(modalProps.type); return;
	if (modalProps.type == 'layout') {
		modal = <LayoutModal modalProps={modalProps} />;
	} else if (modalProps.type == 'addField') {
		modal = <FieldsModal modalProps={modalProps} />;
	} else if (modalProps.type == 'fieldSettings') {
		modal = <FieldSettings modalProps={modalProps} />;
	}
	if (modalProps.fieldType === 'search') {
		modalClasses = 'searchModal';
	}

	return (
		<ReactModal
			ariaHideApp={false}
			shouldCloseOnEsc={true}
			shouldCloseOnOverlayClick={true}
			portalClassName={`modal-portal ${modalProps.type} ${modalClasses}`}
			className={`modal-container ${modalProps.type}`}
			role="dialog"
			parentSelector={() => document.getElementById('views-container')}
			isOpen={modalProps.isVisible}
		>
			<div className="layoutHead">
				<h2 className="layoutHeading">{modalProps.label} </h2>
				<span
					onClick={modalProps.closeModal}
					className="closeLayoutOption"
				>
					<i className="fas fa-times"></i>
				</span>
			</div>
			{modal}
		</ReactModal>
	);
};

export default Modal;
