import React, { useEffect } from 'react';
import { isEmpty } from 'lodash';
import ReactTooltip from 'react-tooltip';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { DisabledFilterSortFields, FieldWithValues } from '../../Constants';
import 'react-tabs/style/react-tabs.css';
import './index.css';

const FilterSettings = (props) => {
	useEffect(() => {
		ReactTooltip.rebuild();
	});

	const saveFieldWithValuesFilter = (event, index) => {
		if (
			event.target.value === 'custom' ||
			event.target.className === 'resetValueType'
		) {
			viewSettingsProps.saveViewSetting(
				event,
				'filter',
				'valueType',
				index
			);
		} else {
			viewSettingsProps.saveViewSetting(event, 'filter', 'value', index);
		}
	};

	const viewSettingsProps = props.viewSettingsProps;
	const formFields = viewSettingsProps.formFields;
	let filterRows = '';
	let view_config = '';
	const { _view_config } = window;
	if (typeof _view_config !== 'undefined') {
		view_config = JSON.parse(_view_config);
	}

	/**
	 * Get Form Fields to show in dropdown
	 */
	const formFieldOptions = Object.keys(formFields).reduce((result, key) => {
		if (!DisabledFilterSortFields.includes(formFields[key].fieldType)) {
			result.push(
				<option key={`frmfieldoption${key}`} value={formFields[key].id}>
					{formFields[key].label}
				</option>
			);
		}
		return result;
	}, []);

	/**
	 * Show Add New Filter button if no filter values present
	 */

	if (isEmpty(viewSettingsProps.viewSettings.filter)) {
		filterRows = (
			<button
				onClick={(event) =>
					viewSettingsProps.addConditionRow(event, 'filter')
				}
				className="pure-button"
			>
				Add
			</button>
		);
	} else {
		filterRows = viewSettingsProps.viewSettings.filter.map(
			(filter, index) => {
				if (typeof formFields[filter.field] === 'undefined') {
					viewSettingsProps.removeConditionRow('', 'filter', index);
					return '';
				}
				let mergeTagsIcon = '';
				if (
					view_config.prefix != 'cf7' &&
					(!FieldWithValues.includes(
						formFields[filter.field].fieldType
					) ||
						filter.valueType === 'custom')
				) {
					mergeTagsIcon = (
						<i
							data-tip={index}
							data-event="click"
							data-for="mergeTags"
							className="fas fa-bars mergeTagsIcon"
						></i>
					);
				} else {
					mergeTagsIcon = '';
				}
				let compareOperators = [
					<option value="=">equal to</option>,
					<option value="!=">NOT equal to</option>,
					<option value=">=">greater than or equal to</option>,
					<option value="<=">less than or equal to</option>,
				];
				if (filter.field !== 'entryDate') {
					compareOperators.push(
						<option value=">">greater than</option>
					);
					compareOperators.push(<option value="<">less than</option>);
					compareOperators.push(
						<option value="LIKE">contains</option>
					);
				}

				let FilterValueField = [];

				FilterValueField.push(
					<input
						className="filterValue"
						key={`filterValue${index}`}
						onChange={(event) =>
							viewSettingsProps.saveViewSetting(
								event,
								'filter',
								'value',
								index
							)
						}
						value={filter.value}
						type="text"
					/>
				);

				if (
					FieldWithValues.includes(
						formFields[filter.field].fieldType
					) &&
					filter.valueType !== 'custom'
				) {
					FilterValueField = (
						<select
							className="filterValue"
							value={filter.value}
							onChange={(event) =>
								saveFieldWithValuesFilter(event, index)
							}
						>
							<option></option>
							{Object.keys(formFields[filter.field].values).map(
								(fieldValue, ind) => {
									return (
										<option
											key={`frmfieldvalue${ind}`}
											value={fieldValue}
										>
											{
												formFields[filter.field].values[
													fieldValue
												]
											}
										</option>
									);
								}
							)}
							<option
								key={`customfilterVal${index}`}
								value="custom"
							>
								{' '}
								Custom Value{' '}
							</option>
						</select>
					);
				}

				let resetCustomValue = [];
				if (filter.valueType === 'custom') {
					resetCustomValue.push(
						<div className="resetValueTypeCont">
							<button
								key={`resetcustomfilterVal${index}`}
								value=""
								className="resetValueType"
								onClick={(event) =>
									saveFieldWithValuesFilter(event, index)
								}
							>
								back
							</button>
						</div>
					);
				}

				return (
					<div key={`filterRow${index}`} className="filterRow">
						<select
							onChange={(event) =>
								viewSettingsProps.saveViewSetting(
									event,
									'filter',
									'field',
									index
								)
							}
							value={filter.field}
						>
							<optgroup label="Form Fields">
								{formFieldOptions}
							</optgroup>
						</select>
						is
						<select
							onChange={(event) =>
								viewSettingsProps.saveViewSetting(
									event,
									'filter',
									'is',
									index
								)
							}
							value={filter.is}
							className="filterConditionSelect"
						>
							{compareOperators}
						</select>
						{FilterValueField}
						{mergeTagsIcon}
						<i
							onClick={(event) =>
								viewSettingsProps.addConditionRow(
									event,
									'filter'
								)
							}
							className="fas fa-plus-circle"
						></i>
						<i
							onClick={(event) =>
								viewSettingsProps.removeConditionRow(
									event,
									'filter',
									index
								)
							}
							className="fas fa-minus-circle"
						></i>
						{resetCustomValue}
					</div>
				);

				// onChange={(e)=> viewSettingsProps.saveViewSetting(e, 'sort') } value={viewSettingsProps.viewSettings.sort.direction}
			}
		);
	}

	// Return Filter
	return (
		<div className="pure-form filterCont viewInlineSec settings-row">
			<label className="">Filter</label>

			<div className="filterRows">{filterRows}</div>
			<ReactTooltip
				clickable={true}
				globalEventOff="click"
				className="mergeTagsToolTip"
				id="mergeTags"
				type="error"
				getContent={(index) => (
					<Tabs>
						<TabList>
							<Tab> Page/Post</Tab>
							<Tab> User </Tab>
							<Tab> Date </Tab>
						</TabList>
						<TabPanel>
							<ul>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:post_id}'
										)
									}
								>
									<span data-tag="{wp:post_id}">
										Post ID <small>{'{wp:post_id}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:post_title}'
										)
									}
								>
									<span data-tag="{wp:post_title}">
										Post Title{' '}
										<small>{'{wp:post_title}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:post_url}'
										)
									}
								>
									<span data-tag="{wp:post_url}">
										Post URL{' '}
										<small>{'{wp:post_url}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:post_author}'
										)
									}
								>
									<span data-tag="{wp:post_author}">
										Post Author{' '}
										<small>{'{wp:post_author}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:post_author_email}'
										)
									}
								>
									<span data-tag="{wp:post_author_email}">
										Post Author Email{' '}
										<small>
											{'{wp:post_author_email}'}
										</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{post_meta:YOUR_META_KEY}'
										)
									}
								>
									<span data-tag="{post_meta:YOUR_META_KEY}">
										Post Meta{' '}
										<small>
											{'{post_meta:YOUR_META_KEY}'}
										</small>
									</span>
								</li>
							</ul>
						</TabPanel>
						<TabPanel>
							<ul>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_id}'
										)
									}
								>
									<span data-tag="{wp:user_id}">
										User ID <small>{'{wp:user_id}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_first_name}'
										)
									}
								>
									<span data-tag="{wp:user_first_name}">
										User First Name{' '}
										<small>{'{wp:user_first_name}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_last_name}'
										)
									}
								>
									<span data-tag="{wp:user_last_name}">
										User Last Name{' '}
										<small>{'{wp:user_last_name}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_display_name}'
										)
									}
								>
									<span data-tag="{wp:user_display_name}">
										User Display Name{' '}
										<small>
											{'{wp:user_display_name}'}
										</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_username}'
										)
									}
								>
									<span data-tag="{wp:user_username}">
										User Username{' '}
										<small>{'{wp:user_username}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_email}'
										)
									}
								>
									<span data-tag="{wp:user_email}">
										User Email{' '}
										<small>{'{wp:user_email}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:user_url}'
										)
									}
								>
									<span data-tag="{wp:user_url}">
										User URL{' '}
										<small>{'{wp:user_url}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{user_meta:YOUR_META_KEY}'
										)
									}
								>
									<span data-tag="{user_meta:YOUR_META_KEY}">
										User Meta{' '}
										<small>
											{'{user_meta:YOUR_META_KEY}'}
										</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:site_title}'
										)
									}
								>
									<span data-tag="{wp:site_title}">
										Site Title{' '}
										<small>{'{wp:site_title}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:site_url}'
										)
									}
								>
									<span data-tag="{wp:site_url}">
										Site URL{' '}
										<small>{'{wp:site_url}'}</small>
									</span>
								</li>
								<li
									onClick={(e) =>
										viewSettingsProps.addMergeTagToFilterInput(
											e,
											index,
											'{wp:admin_email}'
										)
									}
								>
									<span data-tag="{wp:admin_email}">
										Admin Email{' '}
										<small>{'{wp:admin_email}'}</small>
									</span>
								</li>
							</ul>
						</TabPanel>
						{
							<TabPanel>
								<ul>
									<li
										onClick={(e) =>
											viewSettingsProps.addMergeTagToFilterInput(
												e,
												index,
												'{date:today}'
											)
										}
									>
										<span data-tag="{date:today}">
											Today's Date{' '}
											<small>{'{date:today}'}</small>
										</span>
									</li>
									<li
										onClick={(e) =>
											viewSettingsProps.addMergeTagToFilterInput(
												e,
												index,
												'{date:1 days ago}'
											)
										}
									>
										<span data-tag="{date:1 days ago}">
											Yesterday's Date{' '}
											<small>{'{date:1 days ago}'}</small>
										</span>
									</li>
									<li
										onClick={(e) =>
											viewSettingsProps.addMergeTagToFilterInput(
												e,
												index,
												'{date:-1 week}'
											)
										}
									>
										<span data-tag="{date:-1 week}">
											Last Week{' '}
											<small>{'{date:-1 week}'}</small>
										</span>
									</li>
									<li
										onClick={(e) =>
											viewSettingsProps.addMergeTagToFilterInput(
												e,
												index,
												'{date:15 days ago}'
											)
										}
									>
										<span data-tag="{date:15 days ago}">
											Last 15 Days{' '}
											<small>
												{'{date:15 days ago}'}
											</small>
										</span>
									</li>
									<li>
										{view_config.prefix === 'wpforms' && (
											<div>
												{' '}
												<a
													href="https://formviewswp.com/docs/how-to-filter-wpforms-entries-using-merge-tags/#date-merge-tags"
													target="_blank"
												>
													Learn more about using
													custom date tags in flters{' '}
												</a>{' '}
											</div>
										)}
										{view_config.prefix === 'nf' && (
											<div>
												{' '}
												<a
													href="https://nfviews.com/docs/how-to-use-merge-tags-to-filters-ninja-forms-entries/"
													target="_blank"
												>
													Learn more about using
													custom date tags in flters{' '}
												</a>{' '}
											</div>
										)}
									</li>
								</ul>
							</TabPanel>
						}
					</Tabs>
				)}
			/>
		</div>
	);
};

export default FilterSettings;
