import React, { Component } from 'react';
import { isEmpty, cloneDeep } from 'lodash';
import DatatableButtons from './DatatableButtons';
import EditEntry from './EditEntry';
import SingleEntryLink from './SingleEntryLink';
import DeleteEntry from './DeleteEntry';
import Email from './Email';
import {
	DisabledSearchFields,
	DisabledFilterSortFields,
} from '../../Constants';
import './index.css';

class FieldSettings extends Component {
	constructor(props) {
		super(props);

		const modalProps = this.props.modalProps;
		const fieldSettings = modalProps.fieldSettings;
		if (!isEmpty(fieldSettings.search)) {
			const search = fieldSettings.search;
			this.state = {
				search,
				useCustomLabel: fieldSettings.useCustomLabel,
				enableCalculation: fieldSettings.enableCalculation,
			};
		} else {
			this.state = {
				search: {
					fields: [],
					layout: 'horizontal',
					mode: 'any',
					clearButton: false,
				},
				useCustomLabel: fieldSettings.useCustomLabel,
				enableCalculation: fieldSettings.enableCalculation,
			};
		}
	}

	addSearcRow = (e, fieldSettings) => {
		const search = cloneDeep(this.state.search);
		search.fields.push({
			fieldId: 'all_fields',
			label: 'All Fields',
			inputType: '',
		});
		fieldSettings.search = search;
		this.setState({
			search,
		});
	};

	removeSearcRow = (e, index, fieldSettings) => {
		const search = cloneDeep(this.state.search);
		search.fields.splice(index, 1);
		fieldSettings.search = search;
		this.setState({
			search,
		});
	};

	updateSearchSetting = (e, settingType, fieldType, index, fieldSettings) => {
		const search = cloneDeep(this.state.search);
		const formFields = this.props.modalProps.formFields;
		switch (settingType) {
			case 'fields':
				search[settingType][index][fieldType] = e.target.value;
				// fill the search label field with the field label
				if (
					fieldType === 'fieldId' &&
					typeof formFields[e.target.value] !== 'undefined'
				) {
					search[settingType][index].label =
						formFields[e.target.value].label;
				}
				break;
			default:
				if (e.target.type === 'checkbox') {
					search[settingType] = e.target.checked;
				} else {
					search[settingType] = e.target.value;
				}

				break;
		}
		fieldSettings.search = search;
		this.setState({
			search,
		});
	};

	updateState = (e, fieldSettings, settingName) => {
		if (e.target.type === 'checkbox') {
			if (e.target.checked) {
				if (typeof fieldSettings[settingName] != 'undefined') {
					fieldSettings[settingName].push(e.target.value);
				} else {
					fieldSettings[settingName] = [e.target.value];
				}
			} else {
				if (typeof fieldSettings[settingName] != 'undefined') {
					var filteredAry = fieldSettings[settingName].filter(
						(ele) => ele !== e.target.value
					);
				} else {
					var filteredAry = [
						'copy',
						'csv',
						'excel',
						'pdf',
						'print',
					].filter((ele) => ele !== e.target.value);
				}

				fieldSettings[settingName] = filteredAry;
			}
		} else {
			fieldSettings[settingName] = e.target.value;
		}

		this.setState({
			[settingName]: fieldSettings[settingName],
		});
	};

	updateLabelSetting = (e, fieldSettings) => {
		const settingsName = e.target.className;
		fieldSettings[settingsName] = e.target.checked;
		this.setState({
			[settingsName]: e.target.checked,
		});
	};

	render() {
		let extra_settings = [];
		let defaultSettings = '';
		const modalProps = this.props.modalProps;
		const fieldId = modalProps.currentId;
		const fieldtype = modalProps.fieldType;
		let fieldSettings = modalProps.fieldSettings;
		const formFields = modalProps.formFields;
		const { _view_config } = window;
		const view_config = JSON.parse(_view_config);

		/* Update FieldSettings Variable*/
		const updateSettings = (e) => {
			const settingsName = e.target.className;
			fieldSettings[settingsName] =
				e.target.type === 'checkbox'
					? e.target.checked
					: e.target.value;
		};

		if (fieldtype != 'search') {
			extra_settings.push(
				<div key={`frmfieldsettingcustomlabel`}>
					<div className="pure-control-group">
						<input
							className="useCustomLabel"
							defaultChecked={fieldSettings.useCustomLabel}
							onChange={(event) =>
								this.updateLabelSetting(event, fieldSettings)
							}
							type="checkbox"
						/>
						&nbsp;&nbsp;
						<label className="inline-label">
							Use Custom Label?
						</label>
					</div>
					{this.state.useCustomLabel && (
						<div className="pure-control-group">
							<label> Custom Label</label>
							<input
								className="label"
								defaultValue={fieldSettings.label}
								onChange={updateSettings}
								type="text"
							/>
						</div>
					)}
					{/* Sortable Fields */}
					{modalProps.viewType === 'table' &&
						!DisabledFilterSortFields.includes(fieldtype) && (
							<div className="pure-control-group">
								<input
									className="sortableField"
									defaultChecked={fieldSettings.sortableField}
									onChange={(event) =>
										this.updateLabelSetting(
											event,
											fieldSettings
										)
									}
									type="checkbox"
								/>
								&nbsp;&nbsp;
								<label className="inline-label">
									Make this field Sortable?
								</label>
							</div>
						)}
				</div>
			);
		}

		if (fieldtype === 'html') {
			let description = [];
			if (view_config.prefix === 'wpforms') {
				description = (
					<p class="field-setting-description">
						You can use WPForms Smart Tags to display custom data.
						<a
							href="https://formviewswp.com/docs/how-to-use-wpforms-smart-tags-in-custom-html-field/"
							target="_blank"
						>
							Click to know more
						</a>
					</p>
				);
			}
			extra_settings.push(
				<div key={`frmfieldsettinghtml`} className="pure-control-group">
					<label>Custom HTML</label>
					<textarea
						rows="10"
						cols="30"
						className="html"
						defaultValue={fieldSettings.html}
						onChange={updateSettings}
					></textarea>
					{description}
				</div>
			);
		} else if (fieldtype === 'file') {
			extra_settings.push(
				<div key={`frmfieldsettingfile`} className="pure-control-group">
					<input
						className="showEmbedContent"
						defaultChecked={fieldSettings.showEmbedContent}
						onChange={updateSettings}
						type="checkbox"
					/>
					&nbsp;&nbsp;
					<label className="inline-label">
						Show Embeded content?
					</label>
				</div>
			);
		} else if (fieldtype === 'datatableButtons') {
			extra_settings = [
				<DatatableButtons
					updateState={this.updateState}
					fieldSettings={fieldSettings}
				/>,
			];
		} else if (fieldtype === 'editEntry') {
			extra_settings.push(
				<EditEntry
					defaultLabel={fieldSettings.label}
					updateSettings={updateSettings}
					fieldSettings={fieldSettings}
				/>
			);
		} else if (fieldtype === 'deleteEntry') {
			extra_settings.push(
				<DeleteEntry
					defaultLabel={fieldSettings.label}
					updateSettings={updateSettings}
					fieldSettings={fieldSettings}
				/>
			);
		} else if (fieldtype === 'singleEntryLink') {
			extra_settings.push(
				<SingleEntryLink
					defaultLabel={fieldSettings.label}
					updateSettings={updateSettings}
					fieldSettings={fieldSettings}
				/>
			);
		} else if (fieldtype === 'email') {
			extra_settings.push(
				<Email
					key={`frmfieldsettingEmail`}
					defaultLabel={fieldSettings.label}
					updateSettings={updateSettings}
					fieldSettings={fieldSettings}
				/>
			);
		} else if (fieldtype === 'file-upload' || fieldtype === 'file_upload') {
			/* Display File as  */
			extra_settings.push(
				<div
					key={`frmfieldsettingfileupload`}
					className="pure-control-group"
				>
					<label>Display File as:</label>
					<span>
						<input
							className="displayFileType"
							value="Link"
							checked={
								fieldSettings.displayFileType == 'Link' ||
								!fieldSettings.displayFileType
							}
							onChange={(event) =>
								this.updateState(
									event,
									fieldSettings,
									'displayFileType'
								)
							}
							type="radio"
						/>
						Link
					</span>
					<span>
						<input
							className="displayFileType"
							value="Image"
							checked={fieldSettings.displayFileType == 'Image'}
							onChange={(event) =>
								this.updateState(
									event,
									fieldSettings,
									'displayFileType'
								)
							}
							type="radio"
						/>
						Image
					</span>
				</div>
			);

			if (fieldSettings.displayFileType == 'Image') {
				/* on CLick  */
				extra_settings.push(
					<div
						key={`frmfieldsettingfileuploadonclick`}
						className="pure-control-group"
					>
						<label>On Click:</label>
						<span>
							<input
								className="onClickAction"
								value="doNothing"
								checked={
									fieldSettings.onClickAction ==
										'doNothing' ||
									!fieldSettings.onClickAction
								}
								onChange={(event) =>
									this.updateState(
										event,
										fieldSettings,
										'onClickAction'
									)
								}
								type="radio"
							/>
							Do Nothing
						</span>
						<span>
							<input
								className="onClickAction"
								value="newTab"
								checked={
									fieldSettings.onClickAction == 'newTab'
								}
								onChange={(event) =>
									this.updateState(
										event,
										fieldSettings,
										'onClickAction'
									)
								}
								type="radio"
							/>
							Open in new Tab
						</span>
					</div>
				);
			}
		} else if (fieldtype === 'search') {
			/**
			 *   search: {
			 *   fields:{
			 * 				[0] => {
			 * 				fieldId: fieldId,
			 * 				label: label,
			 * 				inputType: text/checkbox etc
			 * 				}
			 * 				[1] => {
			 * 				fieldId: fieldId,
			 * 				label: label,
			 * 				inputType: text/checkbox etc
			 * 				}
			 *
			 * 			}
			 *   layout: vertical/horizontal
			 *   mode: any/all,
			 * 	clearButton: boolean
			 *
			 * }
			 *
			 */
			const search = cloneDeep(this.state.search);
			let searchRows = '';
			const searchFields = cloneDeep(this.state.search.fields);
			/**
			 * Get Form Fields to show in dropdown
			 */

			const formFieldOptions = Object.keys(formFields).reduce(
				(result, key) => {
					if (
						!DisabledSearchFields.includes(
							formFields[key].fieldType
						)
					) {
						let isAlreadyAdded = searchFields.some(function (
							arrayItem
						) {
							return key === arrayItem.fieldId;
						});
						result.push(
							<option
								disabled={isAlreadyAdded}
								key={`frmfieldoption${key}`}
								value={formFields[key].id}
							>
								{formFields[key].label}
							</option>
						);
					}
					return result;
				},
				[]
			);

			if (isEmpty(searchFields)) {
				searchRows = (
					<div className="add-search-field-cont">
						<button
							onClick={(e) => this.addSearcRow(e, fieldSettings)}
							className="pure-button button-primary button-large"
						>
							Add Search Field
						</button>
					</div>
				);
			} else {
				searchRows = searchFields.map((field, index) => {
					let searchInputTypes = [];
					let searchInputTypesOptions = [];
					let disabledState = true;
					if (typeof formFields[field.fieldId] !== 'undefined') {
						if (
							field.fieldId === 'entryDate' ||
							formFields[field.fieldId]['fieldType'] ===
								'date-time' ||
							formFields[field.fieldId]['fieldType'] === 'date'
						) {
							disabledState = false;
							searchInputTypesOptions.push(
								<option value="date">Date</option>
							);
							searchInputTypesOptions.push(
								<option value="dateRange">Date Range</option>
							);
						} else {
							const nonTextFieldTypes = [
								'select',
								'checkbox',
								'radio',
								'listcheckbox',
								'listradio',
								'listmultiselect',
								'listselect',
								'listcountry',
								'liststate',
							];
							const fieldType =
								formFields[field.fieldId]['fieldType'];
							if (nonTextFieldTypes.includes(fieldType)) {
								searchInputTypesOptions = (
									<option value={fieldType}>
										{fieldType}
									</option>
								);
							} else {
								searchInputTypesOptions = (
									<option value="text">Text</option>
								);
							}
						}

						searchInputTypes = (
							<select
								className="searchInputType"
								disabled={disabledState}
								onChange={(event) =>
									this.updateSearchSetting(
										event,
										'fields',
										'inputType',
										index,
										fieldSettings
									)
								}
								value={field.inputType}
							>
								{searchInputTypesOptions}
							</select>
						);
					} else {
						searchInputTypes = (
							<select
								className="searchInputType"
								disabled
								onChange={(event) =>
									this.updateSearchSetting(
										event,
										'fields',
										'inputType',
										index,
										fieldSettings
									)
								}
								value={field.inputType}
							>
								<option value="text">Text</option>
							</select>
						);
					}

					return (
						<div
							key={`searchwid${index}`}
							className="pure-g search-fields-cont"
						>
							<div className="pure-u-1-3">
								<select
									onChange={(event) =>
										this.updateSearchSetting(
											event,
											'fields',
											'fieldId',
											index,
											fieldSettings
										)
									}
									value={field.fieldId}
								>
									<option value="all_fields">
										All Fields
									</option>
									{formFieldOptions}
								</select>
							</div>
							<div className="pure-u-1-3">
								<input
									className="label"
									value={field.label}
									onChange={(event) =>
										this.updateSearchSetting(
											event,
											'fields',
											'label',
											index,
											fieldSettings
										)
									}
									type="text"
								/>
							</div>
							<div className="pure-u-1-3">
								{searchInputTypes}
								{/* <input className="inputType" value={field.inputType} onChange={( event ) => this.updateSearchSetting( event, 'fields', 'inputType', index, fieldSettings )} type="text" /> */}
								<i
									onClick={(e) =>
										this.addSearcRow(e, fieldSettings)
									}
									className="fas fa-plus-circle"
								></i>
								<i
									onClick={(e) =>
										this.removeSearcRow(
											e,
											index,
											fieldSettings
										)
									}
									className="fas fa-minus-circle"
								></i>
							</div>
						</div>
					);
				});
			}

			extra_settings.push(
				<div
					key={`frmfieldsettingsearch`}
					className="pure-control-group"
				>
					<div className="search-layout-cont">
						<label className="inline-label">Search Layout</label>
						<input
							className="layout"
							value="horizontal"
							checked={search.layout === 'horizontal'}
							onChange={(event) =>
								this.updateSearchSetting(
									event,
									'layout',
									null,
									null,
									fieldSettings
								)
							}
							type="radio"
						/>
						&nbsp;&nbsp;Horizontal&nbsp;&nbsp;&nbsp;&nbsp;
						<input
							className="layout"
							value="vertical"
							checked={search.layout === 'vertical'}
							onChange={(event) =>
								this.updateSearchSetting(
									event,
									'layout',
									null,
									null,
									fieldSettings
								)
							}
							type="radio"
						/>
						&nbsp;&nbsp;Vertical
					</div>
					<div className="pure-g search-widget-cont">
						<div className="pure-u-1-3"> Search Field</div>
						<div className="pure-u-1-3"> Label</div>
						<div className="pure-u-1-3"> Input Type </div>
					</div>
					{searchRows}
					{/* <label className="inline-label">Search Mode</label>
				<select onChange={( event ) => this.updateSearchSetting( event, 'mode', null, null, fieldSettings )} value={search.mode}>
					<option value="any">Any</option>
					<option value="all">All</option>
				</select> */}
					<div className=" clearBtn-cont">
						<input
							className="clearButton"
							checked={search.clearButton}
							onChange={(event) =>
								this.updateSearchSetting(
									event,
									'clearButton',
									null,
									null,
									fieldSettings
								)
							}
							type="checkbox"
						/>
						&nbsp;&nbsp;&nbsp;
						<label className="inline-label">
							Display Clear Button
						</label>
					</div>
				</div>
			);
		}

		if (
			typeof view_config.addons !== 'undefined' &&
			view_config.addons.includes('views_calculations')
		) {
			extra_settings.push(
				<div key={`frmfieldsettingenablecalculation`}>
					<div className="pure-control-group">
						<input
							className="enableCalculation"
							defaultChecked={fieldSettings.enableCalculation}
							onChange={(event) =>
								this.updateLabelSetting(event, fieldSettings)
							}
							type="checkbox"
						/>
						<label className="inline-label">
							{' '}
							Enable Calculation
						</label>
					</div>
					{this.state.enableCalculation && (
						<div className="pure-control-group">
							<label> Label</label>
							<p>
								Add custom text with calculation result. Use{' '}
								{'{result}'} to insert calculation result
							</p>
							<input
								className="calculationLabel"
								placeholder="{result}"
								defaultValue={fieldSettings.calculationLabel}
								onChange={updateSettings}
								type="text"
							/>
						</div>
					)}
				</div>
			);
		}

		extra_settings.push(
			<div key={`frmfieldsettingcssclass`} className="pure-control-group">
				<label> CSS Class</label>
				<input
					className="customClass"
					defaultValue={fieldSettings.customClass}
					onChange={updateSettings}
					type="text"
				/>
			</div>
		);

		return (
			<div className="fieldSettings pure-form pure-form-aligned">
				<fieldset>
					{extra_settings}

					<div className="pure-control-group form-action-btns">
						<button
							className="pure-button pure-button-primary"
							onClick={(event) =>
								modalProps.saveFieldSettings(
									event,
									fieldId,
									fieldSettings
								)
							}
						>
							Save
						</button>
						<button
							className="pure-button"
							onClick={modalProps.closeModal}
						>
							Cancel
						</button>
					</div>
				</fieldset>
			</div>
		);
	}
}

export default FieldSettings;
