import React from 'react';
import './index.css';
const ViewType = (props) => {
	const ViewTypes = props.viewTypes.map((type, index) => {
		return (
			<label key={index}>
				<input
					type="radio"
					value={type}
					checked={props.viewType == type}
					onChange={props.onchange}
				/>{' '}
				{type.charAt(0).toUpperCase() + type.slice(1)}
			</label>
		);
	});
	return (
		<div className="viewSourceCont pure-form viewSectionContainer view-type">
			<legend>Select View Type</legend>
			{ViewTypes}
		</div>
	);
};

export default ViewType;
