import React, { useState } from 'react';
// import './index.css';

const Email = (props) => {
	const updateSettings = props.updateSettings;
	const fieldSettings = props.fieldSettings;
	const [emailClickable, setEmailClickable] = useState(
		fieldSettings.emailClickable
	);
	const [emailSubject, setEmailSubject] = useState(
		fieldSettings.emailSubject
	);
	const [emailBody, setEmailBody] = useState(fieldSettings.emailBody);

	const updateValues = (e) => {
		switch (e.target.id) {
			case 'emailClickable':
				fieldSettings.emailClickable = e.target.checked;
				setEmailClickable(e.target.checked);
				break;
			case 'emailSubject':
				fieldSettings.emailSubject = e.target.value;
				setEmailSubject(e.target.value);
				break;
			case 'emailBody':
				fieldSettings.emailBody = e.target.value;
				setEmailBody(e.target.value);
				break;
		}
	};

	return (
		<div>
			<div className="pure-control-group">
				<input
					id="emailClickable"
					defaultChecked={fieldSettings.emailClickable}
					onChange={updateValues}
					type="checkbox"
				/>
				&nbsp;&nbsp;
				<label className="inline-label">
					Make Email Address Clickable?
				</label>
				<p>Clicking on link will create a new email.</p>
			</div>
			{emailClickable && (
				<div>
					<div className="pure-control-group">
						<label>Email Subject</label>
						<input
							id="emailSubject"
							defaultValue={fieldSettings.emailSubject}
							onChange={updateValues}
							type="text"
						/>
						<p>Default email subject line.</p>
					</div>
					<div className="pure-control-group">
						<label>Email Body</label>
						<textarea
							id="emailBody"
							rows="10"
							cols="40"
							className="html"
							defaultValue={fieldSettings.emailBody}
							onChange={updateValues}
						/>
						<p>Default email subject line.</p>
					</div>
				</div>
			)}
		</div>
	);
};

export default Email;
