import React from 'react';
import './index.css';

const MultipleEntriesSettings = (props) => {
	let approvedSubmissionsField = <div></div>;
	let pluginSpeficSettings = '';
	let inlineEditSetting = '';
	let view_config = '';
	const viewSettingsProps = props.viewSettingsProps;
	const { _view_config } = window;
	if (typeof _view_config !== 'undefined') {
		view_config = JSON.parse(_view_config);
	}

	if (
		typeof view_config.addons !== 'undefined' &&
		view_config.addons.includes('views_approved_submissions')
	) {
		approvedSubmissionsField = (
			<div className="pure-form advance-settings-row">
				<label className="">
					<input
						name="approvedSubmissions"
						type="checkbox"
						checked={
							viewSettingsProps.viewSettings.multipleentries
								.approvedSubmissions
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'approvedSubmissions'
							)
						}
					/>
					<span>Display only Approved Submissions</span>
				</label>
			</div>
		);
	}
	if (view_config.prefix == 'wpforms') {
		pluginSpeficSettings = (
			<div>
				<div className="pure-form advance-settings-row">
					<label className="">
						<input
							name="starred"
							type="checkbox"
							checked={
								viewSettingsProps.viewSettings.multipleentries
									.starred
							}
							onChange={(e) =>
								viewSettingsProps.saveViewSetting(
									e,
									'multipleentries',
									'starred'
								)
							}
						/>
						<span>Display only Starred Entries</span>
					</label>
				</div>
				<div className="pure-form advance-settings-row">
					<label className="">
						<input
							name="viewed"
							type="checkbox"
							checked={
								viewSettingsProps.viewSettings.multipleentries
									.viewed
							}
							onChange={(e) =>
								viewSettingsProps.saveViewSetting(
									e,
									'multipleentries',
									'viewed'
								)
							}
						/>
						<span>Display only Viewed Entries</span>
					</label>
				</div>
			</div>
		);
	}
	if (
		typeof view_config.addons !== 'undefined' &&
		view_config.addons.includes('views_inline_edit')
	) {
		inlineEditSetting = (
			<div className="pure-form advance-settings-row">
				<label className="">
					<input
						name="inlineEdit"
						type="checkbox"
						checked={
							viewSettingsProps.viewSettings.multipleentries
								.inlineEdit
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'inlineEdit'
							)
						}
					/>
					<span>Enable Inline Edit</span>
				</label>
			</div>
		);
	}

	return (
		<div>
			<div className="pure-form advance-settings-row">
				<label className="">
					<span>Entries Limit Per Page</span>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					<input
						name="perPage"
						type="text"
						value={
							viewSettingsProps.viewSettings.multipleentries
								.perPage
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'perPage'
							)
						}
					/>
				</label>
			</div>
			<div className="pure-form advance-settings-row">
				<label className="">
					<span>"No Result" Text</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					<input
						placeholder="No records found"
						name="noRecordsText"
						type="text"
						value={
							viewSettingsProps.viewSettings.multipleentries
								.noRecordsText
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'noRecordsText'
							)
						}
					/>
				</label>
			</div>
			<div className="pure-form advance-settings-row">
				<label className="">
					<input
						name="loggedin"
						type="checkbox"
						checked={
							viewSettingsProps.viewSettings.multipleentries
								.loggedin
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'loggedin'
							)
						}
					/>
					<span>Display Only Logged in User Entries</span>
				</label>
			</div>
			<div className="pure-form advance-settings-row">
				<label className="">
					<input
						name="loggedin"
						type="checkbox"
						checked={
							viewSettingsProps.viewSettings.multipleentries
								.hideDataOnLoad
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'hideDataOnLoad'
							)
						}
					/>
					<span>Hide View Data until search is performed</span>
				</label>
			</div>
			<div className="pure-form advance-settings-row">
				<label className="">
					<input
						name="hideEmptyFields"
						type="checkbox"
						checked={
							viewSettingsProps.viewSettings.multipleentries
								.hideEmptyFields
						}
						onChange={(e) =>
							viewSettingsProps.saveViewSetting(
								e,
								'multipleentries',
								'hideEmptyFields'
							)
						}
					/>
					<span>Hide Empty Fields</span>
				</label>
			</div>
			{approvedSubmissionsField}
			{pluginSpeficSettings}
			{inlineEditSetting}
		</div>
	);
};

export default MultipleEntriesSettings;
